import React, { useState, Fragment, useEffect } from "react";
import { useParams, useHistory, Link } from "react-router-dom";
import axios from "axios";

import changeTitle from "../utils/change-title";
import DataTable from "../components/Tables/DataTable";
import TableActions from "../components/Buttons/TableActions";
import Loader from "../components/UI/Loader";
import Breadcrumb from "../components/UI/Breadcrumb";

const Coupons = () => {
  const [couponsState, setCouponsState] = useState([]);
  const { eventId } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const history = useHistory();

  useEffect(() => {
    changeTitle(
      "Coupon",
      { title: "Create Coupon", path: `/create-coupon/${eventId}?for="event"` },
      history
    );
    axios
      .get(`/geteventcouponslist/${eventId}`)
      .then((res) => {
        const convertedRes = JSON.parse(res.data);
        setCouponsState(convertedRes);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, [eventId, history]);

  return (
    <>
      <Breadcrumb items={["Events", "Coupons"]} />
      {isLoading ? (
        <Loader />
      ) : (
        <DataTable
          heading="Price List"
          titles={[
            "Coupon Code",
            "Discount Percentage",
            "Discount Amount",
            "Actions",
          ]}
        >
          {couponsState.map((item, index) => {
            const editURI = `?EventCouponID=${decodeURIComponent(
              item.EventCouponID
            )}&Code=${item.Code}&DiscountPercentage=${decodeURIComponent(
              item.DiscountPercentage
            )}&DiscountAmount=${decodeURIComponent(item.DiscountAmount)}`;
            return (
              <tr key={Math.random()}>
                <td>{index + 1}</td>
                <td>{item.Code}</td>
                <td>{item.DiscountPercentage}</td>
                <td>{item.DiscountAmount}</td>
                <td>
                  <TableActions>
                    <Link
                      to={`/edit-coupon${editURI}`}
                      className="dropdown-item"
                    >
                      Edit
                    </Link>
                    <button to="/" className="dropdown-item">
                      Delete
                    </button>
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </>
  );
};

export default Coupons;
